@keyframes runningAnimation {
  100% {
    transform: rotate(360deg); } }

.bolt-status {
  fill: currentColor; }
  .bolt-status.success {
    color: rgba(85, 163, 98, 1);
    color: var(--status-success-foreground,rgba(85, 163, 98, 1)); }
    @media screen and (-ms-high-contrast: active) {
      .bolt-status.success {
        fill: rgba(85, 163, 98, 1);
        fill: var(--status-success-foreground,rgba(85, 163, 98, 1)); } }
  .bolt-status.failed {
    color: rgba(205, 74, 69, 1);
    color: var(--status-error-foreground,rgba(205, 74, 69, 1)); }
    @media screen and (-ms-high-contrast: active) {
      .bolt-status.failed {
        fill: rgba(205, 74, 69, 1);
        fill: var(--status-error-foreground,rgba(205, 74, 69, 1)); } }
  .bolt-status.warning {
    color: rgba(250, 157, 45, 1);
    color: var(--status-warning-foreground,rgba(250, 157, 45, 1)); }
    @media screen and (-ms-high-contrast: active) {
      .bolt-status.warning {
        fill: rgba(250, 157, 45, 1);
        fill: var(--status-warning-foreground,rgba(250, 157, 45, 1)); } }
  .bolt-status.active {
    color: rgba(0, 120, 212, 1);
    color: var(--status-info-foreground,rgba(0, 120, 212, 1)); }
    @media screen and (-ms-high-contrast: active) {
      .bolt-status.active {
        fill: rgba(0, 120, 212, 1);
        fill: var(--status-info-foreground,rgba(0, 120, 212, 1)); } }
  .bolt-status.neutral {
    color: rgba( 102, 102, 102 ,  1 );
    color: rgba( var(--palette-neutral-60,102, 102, 102) ,  1 ); }
    @media screen and (-ms-high-contrast: active) {
      .bolt-status.neutral {
        fill: rgba( 102, 102, 102 ,  1 );
        fill: rgba( var(--palette-neutral-60,102, 102, 102) ,  1 ); } }
  .bolt-status.animate {
    animation: runningAnimation 8s infinite linear; }
  .bolt-status.small-animate {
    animation: runningAnimation 4s infinite linear; }
  .bolt-status.rotate {
    transform: rotate(180deg); }

.bolt-status-text {
  padding-right: 12px;
  padding-top: 3px;
  padding-bottom: 5px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }
  .bolt-status-text.xl {
    padding-right: 16px;
    padding-top: 5px;
    padding-bottom: 7px; }

.bolt-status-text-extra-padding {
  padding-left: 8px; }

.bolt-status-extended {
  color: rgba( 255, 255, 255 ,  1 );
  color: rgba( var(--palette-neutral-0,255, 255, 255) ,  1 );
  height: 24px;
  border-radius: 15px; }
  .bolt-status-extended.xl {
